
import { computed, defineComponent, ref } from "vue";
import { useStore } from "@/store";
import { useRoute, useRouter } from "vue-router";
import { mapPathToBreadcrumb } from "@/utils/mapMenu";
import localCache from "@/utils/cache";

export default defineComponent({
  emits: ["menuChange"],
  setup(props, { emit }) {
    const isFold = ref(false);
    const store = useStore();
    const username = computed(() => store.state.login.userInfo.name);
    let breadCrumbs: any = [];

    // ä½¿ç”¨è®¡ç®—å±žæ€§ï¼šæ¯æ¬¡ç‚¹å‡»æ›´æ–°å½“å‰è·¯ç”±ä¿¡æ¯
    breadCrumbs = computed(() => {
      // æ³¨æ„è¿™é‡Œæ¯æ¬¡ç‚¹å‡»èœå•æ åº”æ‹¿åˆ°æœ€æ–°çš„ useRouteã€userMenu å’Œ currentPathï¼š
      const route = useRoute();
      const userMenu = store.state.login.userMenu;
      const currentPath = route.path;
      return mapPathToBreadcrumb(userMenu, currentPath);
    });

    const handleMenuFold = () => {
      isFold.value = !isFold.value;
      emit("menuChange", isFold.value);
    };

    const router = useRouter();

    const logout = () => {
      localCache.removeCache("token");
      router.push("/main");
    };

    return {
      isFold,
      username,
      breadCrumbs,
      handleMenuFold,
      logout
    };
  }
});
